    <p class="about_text">徐福记公司简介</p>
    <div class="contain">
      <div class="about_img">
        <img src="../assets/徐福记.png" alt="" />
        <p>
          　　徐福记是由来台湾的徐氏兄弟——曾分别在台湾<a
            href="/wiki/%E7%BB%8F%E8%90%A5"
            title="经营"
            >经营</a
          >徐记食品,徐福记食品、<a href="/wiki/%E5%AE%89%E5%8F%AF" title="安可"
            >安可</a
          >食品、巧比食品等知名<a href="/wiki/%E5%93%81%E7%89%8C" title="品牌"
            >品牌</a
          >,被誉为”二十年金字<a href="/wiki/%E6%8B%9B%E7%89%8C" title="招牌"
            >招牌</a
          >。于1992年在中国广东省东莞市注册创立东莞徐福记食品有限公司”。凭借16年(1976-1992)成功的<a
            href="/wiki/%E5%B8%82%E5%9C%BA"
            title="市场"
            >市场</a
          ><a href="/wiki/%E9%94%80%E5%94%AE" title="销售">销售</a
          >经验，开拓新局，快速获得成长扩展。1997年徐福记与新加坡汇亚集团(TRANSPAC)及仲华<a
            href="/wiki/%E6%B5%B7%E5%A4%96%E6%8A%95%E8%B5%84%E5%9F%BA%E9%87%91"
            title="海外投资基金"
            >海外投资基金</a
          >共同<a href="/wiki/%E5%90%88%E8%B5%84" title="合资">合资</a
          >，为集团带来崭新<a href="/wiki/%E6%80%9D%E7%BB%B4" title="思维"
            >思维</a
          >及理念，共同成立
          BVI徐福记国际集团，并注册成立东莞徐记食品有限公司，专事经营徐福记品牌在中国糖点领域的<a
            href="/wiki/%E6%B0%B8%E7%BB%AD%E5%8F%91%E5%B1%95"
            title="永续发展"
            >永续发展</a
          >。2000年，徐福记正式涉足现代<a
            href="/wiki/%E6%B8%A0%E9%81%93"
            title="渠道"
            >渠道</a
          >的<a
            href="/wiki/%E7%BB%8F%E8%90%A5%E7%AE%A1%E7%90%86"
            title="经营管理"
            >经营管理</a
          >。为集团在中国<a href="/wiki/%E9%9B%B6%E5%94%AE" title="零售">零售</a
          ><a href="/wiki/%E5%B8%82%E5%9C%BA" title="市场">市场</a
          >的大步拓展，扎下厚实的根基。2012年徐福记在中国拥有138家销售<a
            href="/wiki/%E5%88%86%E5%85%AC%E5%8F%B8"
            title="分公司"
            >分公司</a
          >，超过28000个可掌控的终端零售点,成为中国最大的糖果品牌和糖点<a
            href="/wiki/%E4%BC%81%E4%B8%9A"
            title="企业"
            >企业</a
          >之一。
        </p>
        <p>
          　　徐福记主要的生产基地坐落于东莞，工厂占地总面积超过50万平方米，拥有47个大型现代化车间，220条一流自动化生产线，736台尖端的高速包装设备，每日产能超过1500吨。
        </p>
        <h2>徐福记的销售状况</h2>
        <p>
          　　公司在全国拥有138家销售分公司，9000多名销售团队。<a
            href="/wiki/%E4%BA%A7%E5%93%81%E9%94%80%E5%94%AE"
            title="产品销售"
            >产品销售</a
          >网络覆盖全国31个省、市、自冶区，经营及管理现代化渠道超过28000个可掌控的终端零售点。<a
            href="/wiki/%E7%BB%88%E7%AB%AF%E9%94%80%E5%94%AE"
            title="终端销售"
            >终端销售</a
          >网点每年以30%的速率成长，包括：跨国<a
            href="/wiki/%E8%BF%9E%E9%94%81%E9%94%80%E5%94%AE"
            title="连锁销售"
            >连锁销售</a
          ><a href="/wiki/%E7%B3%BB%E7%BB%9F" title="系统">系统</a
          >(如沃尔玛等)；大中型<a href="/wiki/%E8%BF%9E%E9%94%81" title="连锁"
            >连锁</a
          ><a href="/wiki/%E8%B6%85%E5%B8%82" title="超市">超市</a
          >；内、外资零售卖场；量贩店；传统商场；<a
            href="/wiki/%E7%99%BE%E8%B4%A7%E5%85%AC%E5%8F%B8"
            title="百货公司"
            >百货公司</a
          >等，达到8000余家。另外更渗透进入<a
            href="/wiki/%E4%BE%BF%E5%88%A9%E5%BA%97"
            title="便利店"
            >便利店</a
          >、士多店、<a
            href="/wiki/%E6%89%B9%E5%8F%91%E5%B8%82%E5%9C%BA"
            title="批发市场"
            >批发市场</a
          >。其中，主要的<a
            href="/wiki/%E5%A4%A7%E5%8D%96%E5%9C%BA"
            title="大卖场"
            >大卖场</a
          >达到1500家以上，一、二级城市的二卖场约6500余家。目前全国拥有超过4000个配有专属促销员的特色散装专柜门店，分布在大型连锁量贩店及超市等现代化管道。
        </p>
        <p>
          　　2002年初公司取得了沃尔玛公司<a
            href="/wiki/%E5%85%A8%E7%90%83%E9%87%87%E8%B4%AD"
            title="全球采购"
            >全球采购</a
          >系统合格<a href="/wiki/%E4%BE%9B%E5%BA%94%E5%95%86" title="供应商"
            >供应商</a
          >的资格，于2003年全面<a href="/wiki/%E4%BE%9B%E5%BA%94" title="供应"
            >供应</a
          >。“徐福记”糖果糕点透过沃尔玛在全球3000多个超级市场大力拓展国外市场，这大大提高了“徐福记”食品的<a
            href="/wiki/%E5%93%81%E7%89%8C%E6%95%88%E5%BA%94"
            title="品牌效应"
            >品牌效应</a
          >，扩大了产品的出口量，提高了产品在全球市场的占有率。
        </p>
        <p>
          　　公司具有完善的<a href="/wiki/%E6%9C%8D%E5%8A%A1" title="服务"
            >服务</a
          >体系，产品质量完全符合相应<a
            href="/wiki/%E4%BA%A7%E5%93%81%E6%A0%87%E5%87%86"
            title="产品标准"
            >产品标准</a
          >，<a
            href="/wiki/%E6%B6%88%E8%B4%B9%E8%80%85%E6%8A%95%E8%AF%89"
            title="消费者投诉"
            >消费者投诉</a
          >处理率100%。
        </p>
        <h2>徐福记的品牌质量</h2>
        <p>
          　　徐福记以“质量第一，安全满分”建立完善的<a
            href="/wiki/%E8%B4%A8%E9%87%8F"
            title="质量"
            >质量</a
          >保障体系，将ISO9001：2000、HACCP、出口食品卫生注册三大<a
            href="/wiki/%E8%B4%A8%E9%87%8F%E7%AE%A1%E7%90%86%E4%BD%93%E7%B3%BB"
            title="质量管理体系"
            >质量管理体系</a
          >实施有效<a href="/wiki/%E6%95%B4%E5%90%88" title="整合">整合</a
          >，严格<a href="/wiki/%E5%87%86%E7%A1%AE" title="准确">准确</a
          >地掌握及防范危害关键点，超过50名以上的专业品保<a
            href="/wiki/%E5%9B%A2%E9%98%9F"
            title="团队"
            >团队</a
          >，全面掌控、确保产品的优良<a
            href="/wiki/%E5%93%81%E8%B4%A8"
            title="品质"
            >品质</a
          >。<a href="/wiki/%E9%95%BF%E6%9C%9F" title="长期">长期</a
          >以来成功地建立了徐福记的金字招牌，成为中国新年糖果的<a
            href="/wiki/%E9%A2%86%E5%AF%BC%E5%93%81%E7%89%8C"
            title="领导品牌"
            >领导品牌</a
          >。质量稳定、安全健康的徐福记优质产品，不断赢取<a
            href="/wiki/%E6%B6%88%E8%B4%B9"
            title="消费"
            >消费</a
          ><a href="/wiki/%E5%A4%A7%E4%BC%97" title="大众">大众</a
          >的长期信赖与支持，更是徐福记<a
            href="/wiki/%E5%93%81%E7%89%8C%E4%BB%B7%E5%80%BC"
            title="品牌价值"
            >品牌价值</a
          >得以巩固坚稳不坠的基础。
        </p>
        <h2>徐福记的产品系列</h2>
        <p>
          　　专为中国传统新年的喜气而设计研制的徐福记新年糖，开创了独有的新年糖文化，一直都是新年糖果的代名词与领导品牌。徐福记奇欧比巧克力，专为中国人开发研制的80多款特色品种，为春节带来了时尚喜气和顶级享受，增添尊贵和大方。柔软爽滑、香浓爽口的徐福记果冻布丁，新鲜丰富充满休闲风味，每一口都是难忘的滋味。徐福记还有130多款顶级的休闲饼点，是日常生活或工作休闲时的随手点心，每次都可以吃不一样的选择，让紧张的现代生活不时充满高低起伏的愉悦变化。还有，吃一口就记得的徐福记沙琪玛，以纯正的新鲜鸡蛋及上等用料，加上独到的工艺技术制作，多年来早已成为广大消费群众早餐、宵夜好点心的绝佳良伴。
        </p>
